/**
 *  Cart
 *    Functions to handle all cart events.
 *
 *  Version
 *    1.0.1  - 2021/12/16
 */

//@ts-ignore
import $ from 'jquery';
import { getCurrentCart, printMoney, updateCart } from '@process-creative/slate-theme-tools';
import { t } from '../../tools/utilities/string-tools';
import { shopifyAjaxErrorGet } from '../../tools/utilities/error';
import { initAccordion } from '../../objects/accordion/accordion';
import { cartAdd, ON_CART_FINISHED, removeFromCart, pictureGenerate } from "@process-creative/slate-theme-tools";
import { CLASS_SCROLL_DISABLED, CLASS_DRAWER_OPEN, CLASS_CART_PENDING, CLASS_VISIBLE } from '../../tools/constants/classes';
import { findTarget } from '../../tools/utilities/event-tools';
import { Cart, LineItem, Product } from '../../types';
import { RecommndedProduct } from '../../types/recommended-product';
import { KEY_MANUALLY_REMOVED_FREE_GIFT, setItemWithExpiry } from '../../tools/utilities/storage-tools';
import { getShopifyLocation, ShopifyBrowsingContext } from '../../tools/utilities/geolocation-tools';
import { updateThumbnailButtons } from '../../objects/product/ProductThumbnail';
// Selectors
const SELECTOR_CONTAINER = '[data-cart-container]';
const SELECTOR_PRODUCT = '[data-cart-product]';
const SELECTOR_HEADER = '[data-header]'
const SELECTOR_TOGGLE = '[data-cart-drawer-toggle]';
const SELECTOR_HEADER_CART = '[data-cart-drawer-toggle]';
const SELECTOR_SHOPIFY_SECTION = '#shopify-section-cart-contents';
const SELECTOR_UPSELLS = '[data-cart-upsells]';
const SELECTOR_UPSELL_ADD = '[data-cart-upsell-add-btn]';
const SELECTOR_UPSELL_PRODUCT = '[data-cart-upsell-product]';
const SELECTOR_REMOVE_BTN = '[data-cart-remove-btn]';
const SELECTOR_QTY_INPUT = '[data-quantity-selector-field]';
const SELECTOR_QTY_PLUS = '[data-quantity-selector-plus]';
const SELECTOR_QTY_MINUS = '[data-quantity-selector-minus]';
const SELECTOR_CART_NOTES = '[data-cart-notes]';
const SELECTOR_GIFT_NOTE = '[data-gift-note]';
const SELECTOR_ORDER_NOTE = '[data-order-note]';
const SELECTOR_ENVIRONMENT_CHECKBOX = '[data-environment-checkbox]';
const SELECTOR_UPSELLS_CONTAINER = '[data-cart-upsells-container]';
const SELECTOR_DRAWER_DATA = '[data-cart-drawer-data]';
const SELECTOR_LINE_ITEM_JSON = '[data-cart-item-json]';

//Attributes
const ATTR_VARIANT_ID = 'data-variant-id';
const ATTR_LINE_ITEM_INDEX = 'data-cart-remove';
const ATTR_PRODUCT_ID = 'data-cart-product';
const ATTR_CART_TEMPLATE = 'data-cart-template';

// Strings
const STR_ADD = t('products.product.add_to_cart');

// Classes
const CLASS_ADDED = 'is-added';
const CLASS_SHAKING = 'is-shaking';

const UPSELL_PRODUCTS_FETCH_LIMIT = 10; //products to fetch
let UPSELL_PRODUCTS_LIMIT = 0; // products to display, first product will be from Lucky Dip Collection

const body = document.body!;

// Set country body class
getShopifyLocation().then(location => {
  const countryCode = location?.detected_values?.country?.handle?.toLowerCase();
  if (countryCode && countryCode == 'au') {
    body.classList.add(`is-location-${countryCode}`);
  } else {
    body.classList.add(`is-location-int`);
  }
});

document.querySelectorAll<HTMLDivElement>(SELECTOR_CONTAINER).forEach(async container => {
  let contentsRendered = false;
  let cartOpen = false;
  let cartPage = false;

  /**
   *  Elements
   */
  const headerCart:  NodeListOf<HTMLElement> = document.querySelectorAll(SELECTOR_HEADER_CART);
  const header:  NodeListOf<HTMLElement> = document.querySelectorAll(SELECTOR_HEADER);
  let toggleBtns: NodeListOf<HTMLButtonElement>;
  let upsellAddBtns: NodeListOf<HTMLButtonElement>;
  let removeButtons: NodeListOf<HTMLButtonElement>;
  let qtyPlusBtns: NodeListOf<HTMLElement>;
  let qtyMinusBtns: NodeListOf<HTMLElement>;
  let cartNotes: NodeListOf<HTMLElement>;
  let giftInput: NodeListOf<HTMLTextAreaElement>;
  let noteInput: NodeListOf<HTMLTextAreaElement>;
  let environmentCheckbox: NodeListOf<HTMLInputElement>;

  // Render upsells on cart page
  if (container.hasAttribute(ATTR_CART_TEMPLATE)) cartPage = true;

  if (cartPage) await addUpsells();

  // setProgressByCountry();

  addEventListeners();

  $(document).on(ON_CART_FINISHED, onCartFinished);

  function addEventListeners() {
    refreshElements();
    toggleBtns.forEach(btn => btn.addEventListener('click', onCartToggle));
    upsellAddBtns.forEach(btn => btn.addEventListener('click', onUpsellAdd));
    removeButtons.forEach(btn => btn.addEventListener('click', onItemRemove));
    qtyPlusBtns.forEach(btn => btn.addEventListener('click', onQtyPlus));
    qtyMinusBtns.forEach(btn => btn.addEventListener('click', onQtyMinus));
    cartNotes.forEach(accordion => initAccordion({ container: accordion }));
    giftInput.forEach(input => input.addEventListener('blur', saveCartAttributes));
    noteInput.forEach(input => input.addEventListener('blur', saveCartAttributes));
    environmentCheckbox.forEach(input => input.addEventListener('change', saveCartAttributes));
  }

  function removeEventListeners() {
    if (toggleBtns) toggleBtns.forEach(btn => btn.removeEventListener('click', onCartToggle));
    if (upsellAddBtns) upsellAddBtns.forEach(btn => btn.removeEventListener('click', onUpsellAdd));
    if (removeButtons) removeButtons.forEach(btn => btn.removeEventListener('click', onItemRemove));
    if (qtyPlusBtns) qtyPlusBtns.forEach(btn => btn.removeEventListener('click', onQtyPlus));
    if (qtyMinusBtns) qtyMinusBtns.forEach(btn => btn.removeEventListener('click', onQtyMinus));
    if (giftInput) giftInput.forEach(input => input.removeEventListener('blur', saveCartAttributes));
    if (noteInput) noteInput.forEach(input => input.removeEventListener('blur', saveCartAttributes));
    if (environmentCheckbox) environmentCheckbox.forEach(input => input.removeEventListener('change', saveCartAttributes));
  }

  /**
   * Assigns variables
   */
  function refreshElements() {
    toggleBtns = document.querySelectorAll(SELECTOR_TOGGLE);
    upsellAddBtns = container.querySelectorAll(SELECTOR_UPSELL_ADD);
    removeButtons = container.querySelectorAll(SELECTOR_REMOVE_BTN);
    qtyPlusBtns = container.querySelectorAll(SELECTOR_QTY_PLUS);
    qtyMinusBtns = container.querySelectorAll(SELECTOR_QTY_MINUS);
    cartNotes = container.querySelectorAll(SELECTOR_CART_NOTES);
    giftInput = container.querySelectorAll(SELECTOR_GIFT_NOTE);
    noteInput = container.querySelectorAll(SELECTOR_ORDER_NOTE);
    environmentCheckbox = container.querySelectorAll(SELECTOR_ENVIRONMENT_CHECKBOX);
  }

  /**
   * Calls function to render cart
   * Add animation class to cart header icon
   */
  function onCartFinished() {
    renderCartContents();
    if (!headerCart || !header) return;
    header.forEach(headerElement => {
      if (!headerElement.classList.contains(CLASS_VISIBLE)){
        headerElement.classList.add(CLASS_VISIBLE);
        setTimeout(() => {headerElement.classList.remove(CLASS_VISIBLE)}, 2000)
      }

    });
    headerCart.forEach(cartIcon => cartIcon.classList.add(CLASS_SHAKING));
    setTimeout(() => {headerCart.forEach(cartIcon => cartIcon.classList.remove(CLASS_SHAKING))}, 1000)
  }

  /**
   * Fetches first item in the cart
   * Fetches recommended products based on that product's ID
   * Creates HTML string of upsell product thumbnails
   * Returns HTML string
   */
  async function renderUpsellProducts() {
    let html = '';
    const cartFetch = getCurrentCart() as Cart;
    if (!cartFetch.items.length) return;
    const productId = cartFetch.items[0].product_id;
    const productIds = cartFetch.items.map((item: LineItem) => item.product_id);

    let upsellProducts: Product[] = [];

    // Get the upsell product that is defined in the ajax cart section settings
    let cartUpsellData: {
      settings: { [key: string]: string };
      products: { [key: string]: Product };
    } | null = null;
    const elCartData = document.querySelector(SELECTOR_DRAWER_DATA);
    if (elCartData) {
      try {
        cartUpsellData = JSON.parse(elCartData.innerHTML);

        // Check the min spend setting, if the cart is above or equal to that
        // setting, then upsell (so long as the item is not already within the
        // cart)
        const minSpend = cartUpsellData!.settings['upsellMinSpend'];
        if (minSpend && Math.round(cartFetch.total_price / 100) >= parseInt(minSpend)) {
          const matchingProduct = Object.values(cartUpsellData!.products).find(p => {
            return p.handle === cartUpsellData!.settings['upsellProduct'];
          });
          if (matchingProduct) {
            // Ensure the item is not already in the cart.
            if (!cartFetch.items.some(item => {
              return item.handle === matchingProduct.handle
            })) {
              upsellProducts.push(matchingProduct);
            }
          }
        }
      } catch (e) {
        console.error(e);
      }
    }

    if (upsellProducts.length < UPSELL_PRODUCTS_FETCH_LIMIT) {
      const upsellFetchUrl = `/recommendations/products.json?product_id=${productId}&limit=${UPSELL_PRODUCTS_FETCH_LIMIT}`;
      const upsellProductsFetch: RecommndedProduct = await fetch(upsellFetchUrl)
        .then(res => res.json());

      upsellProductsFetch.products.forEach(prod => {
        upsellProducts.push(prod);
      });
    }
    let upsellProductIds = upsellProducts.map(item => item.id);

    // Loop through upsell products to remove
    // any of the products that are already in the cart
    upsellProductIds.forEach((id, i) => {
      if (productIds.includes(id)) {
        upsellProducts.splice(i, 1);
      }
    });

    if (!upsellProducts.length) {
      html = '';
    } else {

      // Cut down to the number of products to display
      upsellProducts.length = UPSELL_PRODUCTS_LIMIT;

      upsellProducts.forEach(product => {
        const { featured_image, price, url, title, variants } = product;
        const variantId = variants[0].id;
        const formattedPrice = printMoney(price);
        const add = t('general.cart.add');
        let strUpsellMessage = '';
        let strUpsellUrl = '';

        html += `
          <div class="c-cart-upsells__product" data-cart-upsell-product data-variant-id="${variantId}">
            <div class="c-cart-upsells__image-container o-responsive-image-container">
              <a href="${url}">
                ${pictureGenerate({
          src: featured_image || '',
          srcSize: 64,
          alt: product.title,
          class: 'c-cart-upsells__image o-responsive-image'
        })}
              </a>
            </div>
            <div class="c-cart-upsells__details-wrapper">
              <div class="c-cart-upsells__details">
                <p class="c-cart-upsells__product-title">
                  <span class="o-paragraph--2">${title}</span>
                </p>
                <p class="c-cart-upsells__price">
                  <span class="o-paragraph--2" data-money=${price}>${formattedPrice}</span>
                  ${strUpsellMessage ?
            `<
                      ${strUpsellUrl ? `a href="${strUpsellUrl}"` : 'span'}
                      class="c-cart-upsells__message"
                    >
                      ${strUpsellMessage}
                    </${strUpsellMessage ? 'a' : 'span'}>`
            : ``}
                </p>
              </div>
              <div class="c-cart-upsells__add-button o-paragraph--2" data-cart-upsell-add-btn>
                <span>${add}</span>
                <svg class="c-cart-upsells__add-icon" fill="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M10 17.5a7.5 7.5 0 1 0 0-15 7.5 7.5 0 0 0 0 15ZM6.875 10h6.25M10 6.875v6.25" stroke="#393939" stroke-linecap="round" stroke-linejoin="round"/></svg>
              </div>
            </div>
          </div>
        `;
      });
      return html;
    }
  }

  /**
   * Toggles open/close state of cart drawer
   * @param e click event
   */
  function onCartToggle(e: Event) {
    e.preventDefault();
    if (!cartOpen) {
      if (!contentsRendered) renderCartContents();
      contentsRendered = true;
      openCart();
    } else {
      closeCart();
    }
  }

  /**
   * Adds upsell product to cart
   * Updates button text
   *
   * @param e Click event
   */
  async function onUpsellAdd(e: Event) {
    const button = findTarget(e, SELECTOR_UPSELL_ADD);;
    const upsellProduct = button?.closest<HTMLElement>(SELECTOR_UPSELL_PRODUCT);
    if (!upsellProduct) return;
    const variantID = upsellProduct.getAttribute(ATTR_VARIANT_ID);
    if (!variantID) throw 'Invalid markup detected in the product upsell snippet.';

    try {
      if (button) button.innerText = t('general.cart.adding');
      await cartAdd({ items: [{ id: parseInt(variantID), quantity: 1 }] });
      updateThumbnailButtons(variantID);
    } catch (e) {
      //Error, show error, enable buttons hide loader
      console.error('Failed to add upsell to cart.');
      return;
    }
  }

  /**
   * Removes item from cart based on the item's line index
   * @param e Click event
   */
  async function onItemRemove(e: Event) {
    e.preventDefault();
    body.classList.add(CLASS_CART_PENDING);
    const button = e.currentTarget as HTMLElement;
    const lineindex = button.getAttribute(ATTR_LINE_ITEM_INDEX);
    if (!lineindex) return;
    const li = parseInt(lineindex);

    const parent = findTarget(e, SELECTOR_PRODUCT);
    if (!parent) return;

    const jsonElem = parent.querySelector<HTMLScriptElement>(SELECTOR_LINE_ITEM_JSON);
    if (!jsonElem) return;

    const variantId = parent.getAttribute(ATTR_PRODUCT_ID);
    if (!variantId) return;

    // When a free gift is removed, keep track of it in browser storage
    const item = JSON.parse(jsonElem.innerHTML) as LineItem;
    if (item.properties['_free-gift'] == 'true') {
      const key = `${KEY_MANUALLY_REMOVED_FREE_GIFT}_${variantId}`;
      setItemWithExpiry(key, 'true');
    }

    try {
      await removeFromCart(li);
      updateThumbnailButtons(variantId);
    } catch (e) {
      console.error(shopifyAjaxErrorGet(e));
    }
    setTimeout(() => body.classList.remove(CLASS_CART_PENDING), 300);
  }

  /**
   *
   * @param e click event
   * @param change string that determines if you're adding or subtracting. Must be add or subtract
   * Get the index of the cart item clicked on, then adds or removes 1 from the quantity depending on argument passed in
   */
  function onQtyChange(e: Event, change: 'add' | 'subtract') {
    const target = e.currentTarget as HTMLElement;
    const parent = target.closest(SELECTOR_PRODUCT);
    if (!parent) return;

    const line = parseInt(parent.getAttribute('data-index') || '0');
    const inputQuantity = parent.querySelector<HTMLInputElement>(SELECTOR_QTY_INPUT);
    if (!line || !inputQuantity) return;

    let quantity = parseInt(inputQuantity.value);

    if (change === 'add') {
      quantity++;
    } else if (change === 'subtract') {
      quantity--;
    }

    updateCart(line, quantity);
    onCartFinished();
  }

  function onQtyPlus(e: Event) {
    onQtyChange(e, 'add');
  }

  function onQtyMinus(e: Event) {
    onQtyChange(e, 'subtract');
  }

  /**
   * Adds a class to open cart drawer
   * Adds class to body to disable scrolling
   */
  function openCart() {
    container.classList.add(CLASS_DRAWER_OPEN);
    body.classList.add(CLASS_DRAWER_OPEN);
    body.classList.add(CLASS_SCROLL_DISABLED);
    cartOpen = true;
  }

  /**
   * Removes a class to close cart drawer
   * Removes class to body to enable scrolling
   */
  function closeCart() {
    container.classList.remove(CLASS_DRAWER_OPEN);
    body.classList.remove(CLASS_DRAWER_OPEN);
    body.classList.remove(CLASS_SCROLL_DISABLED);
    cartOpen = false;
  }

  /**
   * Checks if you're currently on the cart page
   */
  function isCartPage() {
    return window.location.href.includes('/cart');
  }

  /**
   * Grabs the text entered into textareas
   * Gets the checked state of the checkbox
   * Updates cart attributes
   */
  async function saveCartAttributes() {
    // elements appear twice on cart page, so checking to see which instance to save
    let idx;
    cartPage ? idx = 1 : idx = 0;

    const giftMessage = giftInput[idx].value;
    const noteMessage = noteInput[idx].value;
    const environmental = `${environmentCheckbox[idx].checked}`;

    let formData = {
      attributes: {
        'gift-note': giftMessage,
        'order-note': noteMessage,
        'environmental': environmental
      }
    }

    await fetch('/cart/update.js', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(formData)
    }).then(e => e.json());
  }

  async function addUpsells() {
    const upsellContainer = container.querySelector<HTMLDivElement>(SELECTOR_UPSELLS_CONTAINER);
    const upsellElement = container.querySelector<HTMLDivElement>(SELECTOR_UPSELLS);

    // If there's nothing in the upsell container, update the number
    if (upsellElement) {
      const upsellProducts = upsellElement.querySelectorAll(SELECTOR_UPSELL_PRODUCT);
      if (!upsellProducts.length) {
        UPSELL_PRODUCTS_LIMIT = 1;
      } else {
        UPSELL_PRODUCTS_LIMIT = 0;
      }
    }
    let upsellHtml;
    if (UPSELL_PRODUCTS_LIMIT > 0) {
      upsellHtml = await renderUpsellProducts();
    } else {
      upsellHtml = '';
    }

    // Inject the fetched product(s).
    // If upsellHtml doesn't return anything because all
    // fetched products are already in the cart, then hide the upsells
    if (upsellElement) {
      upsellElement.innerHTML += upsellHtml;
    } else if (upsellContainer) {
      upsellContainer.style.display = 'none';
    }
  }

  /**
   *  renderCartContents
   *    Uses the Section Rendering API to render the contents of the cart
   *    drawer and print them in the container for the cart drawer
   */
  async function renderCartContents() {
    //Set global pending state
    body.classList.add(CLASS_CART_PENDING);

    //Remove event listeners and destroy existing upsell sliders
    removeEventListeners();

    const sectionHtml: string = await fetch(`/?sections=cart-contents`)
      .then(res => { return res.json() })
      .then(res => res['cart-contents']);
    const html = document.createElement('div');
    html.innerHTML = sectionHtml;
    const sectionWrapper = html.querySelector<HTMLDivElement>(SELECTOR_SHOPIFY_SECTION);
    if (!sectionWrapper) throw ('Invalid markup detected in the Cart Drawer section.');
    if (cartPage) {
      container.innerHTML = sectionHtml;
    } else {
      container.innerHTML = String(sectionWrapper.innerHTML);
    }

    await addUpsells();

    //DOM changed, refresh listeners
    addEventListeners();

    //Trigger the money change event
    const event = new Event ('onMoneyChange');
    document.dispatchEvent(event);

    //Remove global pending state
    body.classList.remove(CLASS_CART_PENDING);
  }
});
