// Keys
export const KEY_MANUALLY_REMOVED_FREE_GIFT = `manuallyRemovedFreeGift`;

// Helper functions
/**
 * Saves a key/value pair into local storage with an expiry time.
 * @param key A string to use as the key for local storage
 * @param value A string value to be saved in key value pair
 * @param ttl A number to indicate the TTL on how long before the item expires
 */
export const setItemWithExpiry = (key:string, value:string, ttl = 604800) => {
  // By default ttl (Time To Live) is 604800 (7 days)
  const now = new Date();

  /**
   * `item` is an object which contains the original value
   * as well as the time when it's supposed to expire
   */
  const item = {
    value: value,
    expiry: now.getTime() + ttl
  };

  localStorage.setItem(key, JSON.stringify(item));
}

/**
 * Retrieves an item value, but expires it first if necessary
 * @param key A string used to look for the key in local storage
 * @returns A string containing the items value or null if the item expires or
 * if there is no item to be found
 */
export const getItemWithExpiry = (key:string) : string|null => {
  const itemStr = localStorage.getItem(key);

  // If the item doesn't exist, return null
  if(!itemStr) return null;

  const item = JSON.parse(itemStr);
  const now = new Date();

  // Compare the expiry time of the item with the current time
  if(now.getTime() > item.expiry) {
    /**
     * If the item is expired, delete the item from storage
     * and return null
     */
    localStorage.removeItem(key);
    return null;
  }

  return item.value;
}
